import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { MyApp } from './app.component';
import { OwnerHomePage } from '../pages/home/ownerHome/ownerHome';
import { VetHomePage } from '../pages/home/vetHome/vetHome';
import { LoginPage } from "../pages/login/login";
import { RedirectPage } from "../pages/redirect/redirect";
import { AngularFireModule } from 'angularfire2';
import { AngularFireAuthModule }from 'angularfire2/auth';
import { AngularFireDatabase, AngularFireDatabaseModule } from 'angularfire2/database';
import { RegisterOwnerPage } from "../pages/register/registerOwner/register";
import { BookingsPage } from "../pages/bookings/bookings";
import { StorePage } from "../pages/store/store";
import { CalendarPage } from "../pages/calendar/calendar";
import { PetPage } from "../pages/pet/pet";
import { EditProfilePage} from "../pages/editprofile/editprofile";
import { HttpModule } from "@angular/http";
import { ProfilePageModule } from "../pages/profile/profile.module";
import { HttpServiceProvider } from '../providers/http-service/http-service';
import { AvailModal } from '../pages/calendar/availModal';
import { MakeBookingModal } from "../pages/bookings/MakeBookingModal";
import { FindNearestVet} from "../pages/bookings/FindNearestVet";

var config = {
  apiKey: "AIzaSyD16Z_p-S5LgSceHoL3ob7gRNSqqFaPbWQ",
  authDomain: "vetquoll-c22f9.firebaseapp.com",
  databaseURL: "https://vetquoll-c22f9.firebaseio.com",
  projectId: "vetquoll-c22f9",
  storageBucket: "vetquoll-c22f9.appspot.com",
  messagingSenderId: "1063752403506"
};

@NgModule({
  declarations: [
    MyApp,
    LoginPage,
    OwnerHomePage,
    VetHomePage,
    StorePage,
    CalendarPage,
    BookingsPage,
    RedirectPage,
    AvailModal,
    MakeBookingModal,
    FindNearestVet,
    EditProfilePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/pet/pet.module#PetPageModule', name: 'PetPage', segment: 'pet', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/editprofile/editprofile.module#EditProfilePageModule', name: 'EditProfilePage', segment: 'editprofile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/registerPet/registerPet.module#RegisterPetPageModule', name: 'RegisterPetPage', segment: 'registerPet', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module#ProfilePageModule', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/store/store.module#StorePageModule', name: 'StorePage', segment: 'store', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/bookings/bookings.module#BookingsPageModule', name: 'BookingsPage', segment: 'bookings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/calendar/calendar.module#CalendarPageModule', name: 'CalendarPage', segment: 'calendar', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/redirect/redirect.module#RedirectPageModule', name: 'RedirectPage', segment: 'redirect', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/registerOwner/register.module#RegisterPageModule', name: 'RegisterOwnerPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile_vet/profile_Vet.module#ProfileVetPageModule', name: 'ProfileVetPage', segment: 'profile_Vet', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(config),
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    HttpModule,
    ProfilePageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    OwnerHomePage,
    VetHomePage,
    StorePage,
    CalendarPage,
    BookingsPage,
    RedirectPage,
    AvailModal,
    MakeBookingModal,
    FindNearestVet,
    EditProfilePage
  ],
  providers: [
    AngularFireDatabase,
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    HttpServiceProvider
  ]
})
export class AppModule {}



